@import url('https://fonts.googleapis.com/css?family=Roboto:300,400&display=swap');
@import "reset";
body, html {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  font-size: 24px;
  outline: none !important;
  font-family: 'Roboto';
  @media (max-width: 1330px) {
    font-size: 20px;
    line-height: 23px;
  }
  @media (max-width: 640px) {
    font-size: 16px;
    line-height: 19px;
  }
}
* {
  outline: none !important;
  box-sizing: border-box;
}
@keyframes whopperAnimation {
  from {
    transform: scale(1);
  }
  50% {
    transform: scale(1.5);
  }
  to {
    transform: scale(1);
  }
}
@keyframes shadow-animation {
  from {
    width: 322px;
    height: 75px;
  }
  50% {
    width: 200px;
    height: 50px;
  }
  to {
    width: 322px;
    height: 75px
  }
}
@keyframes tooth-animation {
  from {
    top: 0;
  }
  50% {
    top: -35px;
  }
  to {
    top: 0;
  }
}
@keyframes to-top-animation {
  from {
    padding-bottom: 20px;
  }
  50% {
    padding-bottom: 35px;
  }
  to {
    padding-bottom: 20px;
  }
}
a {
  text-decoration: none;
}
.h1 {
  font-size: 2.91rem;
  font-weight: 300;
  line-height: 3.41rem;
  color: #666666;
  @media screen {
    @media (max-width: 1330px) {
      font-size: 2.6rem;
      line-height: 3.05rem;
      @media (max-width: 640px) {
        font-size: 1.5rem;
        line-height: 1.75rem;
        text-align: center;
      }
    }
  }
}
.h2 {
  font-size: 1.91rem;
  color: #666666;
  line-height: 2.25rem;
  font-weight: 400;
  @media screen {
    @media (max-width: 640px) {
      font-size: 1.5rem;
      line-height: 1.75rem;
      margin-bottom: 12px;
    }
  }
}
.h4 {
  font-size: 1rem;
  font-weight: 400;
  line-height: 1.16rem;
  @media screen {
    @media (max-width: 640px) {
      font-size: 0.625rem;
      line-height: .75rem;
    }
  }
}
.h5 {
  font-size: 1.2rem;
}
.h5-light {
  font-size: 1.2rem;
}
.link-text {
  font-size: 1rem;
  line-height: 1.16rem;
  color: #666666;
  text-decoration: none;
  transition: .2s;
  &:hover {
    color: #EB630D;
  }
  @media screen {
    @media (max-width: 640px) {
      font-size: 1.25rem;
      line-height: 1.43rem;
    }
  }
}
.text {
  font-size: 1rem;
  line-height: 1.16rem;
  color: #666666;
  text-decoration: none;
  font-weight: 300;
}
//start header
.header {
  overflow: hidden;
}
.wrapper {
  width: 1312px;
  margin-left: auto;
  margin-right: auto;
  @media screen  {
    @media  (max-width: 1330px){
      width: 620px;
      @media (max-width: 640px) {
        width: 280px;
      }
    }
  }
}
.header-logo {
  width: 172px;
  height: 61px;
  &-mobile {
    display: none;
    @media screen  {
      @media (max-width: 1330px) {
        display: block;
        width: 130px;
        height: 46px;
        margin-left: calc(50% - 310px);
        margin-top: 35px;
        @media (max-width: 640px) {
          width: 90px;
          height: 32px;
          margin-left: calc(50% - 140px);
          margin-top: 17px;
        }
      }
    }
  }
  @media screen  {
    @media (max-width: 1330px) {
      width: 130px;
      height: 46px;
      @media (max-width: 640px) {
        width: 90px;
        height: 32px;
      }
    }
  }
}

.header-container {
  display: flex;
  justify-content: space-between;
  margin-top: 35px;
  @media screen {
    @media (max-width: 1330px) {
      align-items: center;
      @media (max-width: 640px) {
        margin-top: 16px;
      }
    }
  }
  &-right {
    @media screen and (max-width: 1330px) {
      margin-left: 100%;
      position: fixed;
      width: 100vw;
      overflow-x: hidden;
      bottom: 0;
      left: 0;
      background: #f3f3f3;
      z-index: 5;
      transition: .5s;
      height: 100vh;
      height: calc(var(--vh, 1vh) * 100);
    }
  }
  &-phones {
    display: flex;
    justify-content: flex-end;
    margin-top: 17px;
    align-items: flex-end;
    @media screen  {
      @media (max-width: 1330px) {
        flex-direction: column;
        align-items: normal;
        width: 620px;
        margin-left: auto;
        margin-right: auto;
        margin-top: 40px;
        @media (max-width: 640px) {
          width: 280px;
          margin-top: 32px;
        }
      }
    }
    &-li {
      margin-right: 20px;
      @media screen and (max-width: 1330px) {
        margin-bottom: 12px;
        margin-right: 0;
      }
      &:last-child {
        margin-right: 0;
        @media screen and (max-width: 1330px) {
          margin-bottom: 0;
        }
      }
      .link-text {
        font-size: .75rem;
        line-height: .875rem;
        @media screen and (max-width: 1330px){
          font-size: 1rem;
          line-height: 1.16rem;
          @media screen and (max-width: 640px){
            font-size: 1.25rem;
            line-height: 1.43rem;
          }
        }
      }
    }
  }
}
.header-nav-list {
  display: flex;
  @media screen  {
    @media (max-width: 1330px) {
      flex-direction: column;
      width: 620px;
      margin-left: auto;
      margin-right: auto;
      margin-top: 38px;
      @media (max-width: 640px) {
        width: 280px;
      }
    }
  }
  &-li {
    margin-right: 30px;
    @media screen and (max-width: 1330px) {
      margin-right: 0;
      margin-bottom: 12px;
    }
    &:last-child {
      margin-right: 0;
      @media screen and (max-width: 1330px) {
        margin-bottom: 0;
      }
    }
  }
}
.instagram-icon {
  width: 24px;
  height: 24px;
  @media screen and (max-width: 1330px) {
    width: 32px;
    height: 32px;
  }
}
.facebook-icon {
  width: 11px;
  height: 24px;
  margin-right: 10px;
  @media screen and (max-width: 1330px) {
    width: 15px;
    height: 32px;
  }
}
.social-icon {
  transition: .2s;
  display: inline-block;
  &:hover path {
    fill: #EB630D;
  }
}
.whopper {
  height: 26px;
  width: 48px;
  cursor: pointer;
  display: none;
  position: relative;
  overflow: hidden;
  z-index: 6;
  padding: 10px;
  margin-right: -10px;
  box-sizing: content-box;
  @media screen {
    @media (max-width: 1330px) {
      display: block;
      @media (max-width: 640px) {
        width: 36px;
        height: 22px;
      }
    }
  }
  &-line {
    width: 100%;
    display: block;
    height: 4px;
    background: #EB630D;
    margin-top: 6px;
    transition: .5s;
    @media screen {
      @media (max-width: 640px) {
        margin-top: 5px;
      }
    }
    &:first-child {
      margin-top: 0;
    }
  }
}
.mobile-menu-open {
  margin-left: 0 !important;
}
.open-menu {
  width: 26px;
  height: 26px;
  .whopper-line {
    &:nth-child(1) {
      transform-origin: left top;
      transform: rotate(45deg);
      margin: 0;
      position: absolute;
      top: 10px;
      left: 10px;
      width: 33px;
      @media screen {
        @media (max-width: 640px) {
          width: 26px;
        }
      }
    }
    &:nth-child(2) {
      margin-left: 200%;
    }
    &:nth-child(3) {
      transform-origin: left bottom;
      transform: rotate(-45deg);
      margin: 0;
      position: absolute;
      bottom: 10px;
      left: 10px;
      width: 33px;
      @media screen {
        @media (max-width: 640px) {
          width: 26px;
        }
      }
    }
  }
}
//end header
.fixed-background {
  position: fixed;
  width: 100vw;
  height: 100vh;
  overflow: hidden;
  left: 0;
  top: 0;
  background: url(../img/fixed-background.png) no-repeat center center / cover;
  z-index: -1;
}
//start main
.main {
  height: calc(100vh - 107px - 3.33rem);
  margin-top: 3.33rem;
  @media screen {
    @media (max-width: 1330px) {
      height: auto;
    }
  }
  .wrapper {
    height: 100%;
  }
  &-container {
    display: flex;
    justify-content: space-between;
    height: 100%;
    @media screen {
      @media (max-width: 1330px) {
        flex-direction: column;
        justify-content: normal;
      }
    }
    &-left {
      margin-top: 120px;
      @media screen {
        @media (max-width: 1330px) {
          margin-top: 0;
        }
      }
    }
    &-right {
      justify-content: center;
      display: flex;
      flex-direction: column;
      align-items: center;
      position: relative;
      width: 417px;
      align-self: flex-start;
      height: 100%;
      @media screen {
        @media (max-width: 1330px) {
          width: 100%;
          margin-top: 3rem;
        }
      }
    }
  }
}
.orange-button {
  background: #EB630D;
  box-shadow: 5px 5px 10px #BDBDBD, -5px -5px 10px #FFFFFF;
  border-radius: 4px;
  color: #F3F3F3;
  height: 56px;
  cursor: pointer;
  border: none;
  transition: .2s;
  font-size: 0.83rem;
  @media screen {
    @media (max-width: 1330px) {
      margin-top: 32px;
      @media (max-width: 640px) {
        height: 38px;
        width: 247px;
        display: block;
        margin-left: auto;
        margin-right: auto;
      }
    }
  }
  &:hover {
    background: #FD6E13;
    box-shadow: 5px 5px 12px #C5C5C5, -5px -5px 12px #FFFFFF;
  }
  &:active {
    background: #EB630D;
    box-shadow: inset 5px 5px 12px #B74912, inset -5px -5px 12px #FF874B;
  }
}
.white-btn {
  height: 38px;
  box-shadow: 5px 5px 10px #C75922;
  border-radius: 4px;
  background: #F3F3F3;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  padding: 0 16px;
  cursor: pointer;
  color: #666666;
  font-size: .66rem;
  transition: .2s;
  line-height: normal;
  @media screen {
    @media (max-width: 640px) {
      padding: 0 14px;
      font-size: 0.625rem;
      line-height: 0.75rem;
      height: 29px;
    }
  }
  &:hover {
    background: #F3F3F3;
    box-shadow: 5px 5px 10px #C75922;
  }
  &:active {
    background: #FFFFFF;
    box-shadow: inset 5px 5px 12px rgba(197, 197, 197, 0.5), inset -5px -5px 12px #FFFFFF;
  }
}
.main-button {
  width: 322px;
  margin-top: 2.16rem;
}
.tooth {
  z-index: 1;
  position: relative;
  animation: tooth-animation 3s infinite;
  max-height: 80%;
  @media screen {
    @media (max-width: 1330px) {
      width: 242px;
      height: auto;
      @media (max-width: 640px) {
        width: 140px;
      }
    }
  }
  &-shadow {
    width: 322px;
    height: 75px;
    animation: shadow-animation 3s infinite;
    position: absolute;
    bottom: -50px;
    @media screen {
      @media (max-width: 640px) {
        transform: scale(.5);
      }
    }
  }
}
.to-top {
  width: 44px;
  height: 42px;
  position: fixed;
  bottom: 30px;
  right: 30px;
  cursor: pointer;
  transition: .2s;
  padding: 20px;
  z-index: 4;
  box-sizing: content-box;
  @media screen {
    @media (max-width: 1330px) {
      display: none;
    }
  }
  &:hover  {
    animation: to-top-animation 1.5s infinite;
    path {
      fill-opacity: 1;
    }
  }
}
//end main
//start implants
.section-padding {
  padding: 5rem 0;
}
.implants-container {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}
.implant {
  width: 640px;
  border-radius: 4px;
  position: relative;
  box-shadow: 10px 10px 24px #C5C5C5, -10px -10px 24px #FFFFFF;
  transition: .5s;
  @media screen {
    @media (max-width: 1330px) {
      width: 100%;
    }
  }
  &-in {
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    background-color: #fff;
    opacity: 0;
    transition: .4s;
    border-radius: 4px;
    @media screen {
      @media (max-width: 1330px) {
        opacity: 1;
        top: auto;
        bottom: 0;
      }
    }
    .implant-main {
      color: #666666;
      @media screen {
        @media (max-width: 1330px) {
          display: none;
        }
      }
    }
    &-description {
      font-size: 0.83rem;
      line-height: 0.96rem;
      margin-top: 24px;
      margin-left: 28px;
      margin-right: 28px;
      color: #666666;
      @media screen {
        @media (max-width: 1330px) {
          margin-top: 20px;
          font-size: 1rem;
          line-height: 1.16rem;
          @media (max-width: 640px) {
            font-size: .625rem;
            line-height: .75rem;
            margin-left: 12px;
            margin-right: 12px;
          }
        }
      }
    }
    &-img {
      margin-top: 24px;
      margin-left: 28px;
    }
    &-button {
      width: auto;
      padding: 0 33px;
      height: 47px;
      display: inline-flex;
      justify-content: center;
      align-items: center;
      margin-top: 18px;
      margin-left: 28px;
      @media screen {
        @media (max-width: 1330px) {
          margin-top: 20px;
          @media (max-width: 640px) {
            height: 38px;
            padding-left: 0;
            padding-right:0;
            width: 213px;
            display: flex;
          }
        }
      }
    }
  }
  &:hover .implant-in {
    opacity: 1;
  }
  &-1 {
    height: 287px;
    background: #EB630D url(../img/i1.png) no-repeat center bottom;
    @media screen {
      @media (max-width: 1330px) {
        height: 525px;
        background: #EB630D url(../img/i1.png) no-repeat center top 55px;
        @media (max-width: 640px) {
          height: 273px;
          background: #EB630D url(../img/i1.png) no-repeat center top 35px / 180px auto;
          .implant-in {
            height: 150px;
          }
        }
        .implant-in {
          height: 238px;
        }
      }
    }
  }
  &-2 {
    height: 240px;
    background: #EB630D url(../img/i2.png) no-repeat center center / cover;
    transition-delay: .1s;
    @media screen {
      @media (max-width: 1330px) {
        height: 383px;
        background: #EB630D url(../img/i2.png) no-repeat center top ;
        margin-top: 32px;
        @media (max-width: 640px) {
          height: 206px;
          background: #EB630D url(../img/i2.png) no-repeat center top /  100% auto;
          .implant-in {
            height: 126px;
          }
        }
        .implant-in {
          height: 165px;
        }
      }
    }
  }
  &-3 {
    margin-top: 32px;
    height: 604px;
    background: #EB630D url(../img/i3.png) no-repeat center center / cover;
    transition-delay: .4s;
    @media screen {
      @media (max-width: 1330px) {
        height: 462px;
        background: #EB630D;
        order: 6;
        transition-delay: .2s;
        @media (max-width: 640px) {
          height: 225px;
          .implant-in {
            &-description {
              margin-top: 43px;
            }
          }
        }
        .implant-in {
          background: transparent;
          &-description {
            margin-top: 84px;
            color: #F3F3F3;
          }
        }
      }
    }
  }
  &-4 {
    height: 220px;
    background: #EB630D url(../img/i4.png) no-repeat center center / cover;
    transition-delay: .2s;
    @media screen {
      @media (max-width: 1330px) {
        height: 405px;
        background: #EB630D url(../img/i4.png) no-repeat center top / 100% auto;
        order: 6;
        transition-delay: .2s;
        @media (max-width: 640px) {
          height: 222px;
          .implant-in {
            height: 126px;
          }
        }
        .implant-in {
          height: 191px;
        }
      }
    }
  }
  &-5 {
    height: 399px;
    margin-top: 32px;
    background: #EB630D url(../img/i5.png) no-repeat left 15px bottom 20px;
    transition-delay: .4s;
    @media screen {
      @media (max-width: 1330px) {
        height: 739px;
        background: #EB630D url(../img/i5.png) no-repeat left 5px top 50px;
        order: 6;
        transition-delay: .3s;
        @media (max-width: 640px) {
          background: #EB630D url(../img/i5.png) no-repeat left 5px top 35px /  100% auto;
          height: 325px;
          .implant-in {
            height: 157px;
            &-img {
              margin: 12px 12px 0 12px;
              width: calc(100% - 24px)
            }
          }
        }
        .implant-in {
          height: 346px;
        }
      }
    }
  }
  &-main {
    color: #fff;
    margin-left: 28px;
    padding-top: 24px;
    @media screen {
      @media (max-width: 640px) {
        margin-left: 10px;
        padding-top: 14px;
      }
    }
  }
}
.implant-container-4 {
  position: relative;
  top: -13px;
  @media screen {
    @media (max-width: 1330px) {
      top: 0;
      width: 100%;
      margin-top: 32px;
    }
  }
}
.zoom-in {
  opacity: 0;
  transform: scale(0);
  -webkit-transform: scale(0);
  &.show {
    opacity: 1;
    transform: scale(1);
    -webkit-transform: scale(1);
  }
}
//end implants
//main-news
.main-news {
  padding-top: 2rem;
  padding-bottom: 4rem;
  background: #fff;
  box-shadow: 0px 20px 60px rgba(0, 0, 0, 0.25);
  &-btn {
    width: 239px;
    height: 56px;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-left: auto;
    margin-right: auto;
    margin-top: 60px;
    @media screen {
      @media (max-width: 640px) {
        width: 173px;
        height: 38px;
        margin-top: 8px;
        display: flex;
       }
    }
  }
  &.not-shadow {
    box-shadow: none;
  }
  &-list {
    margin-top: 60px;
    &-li {
      display: flex;
      padding-bottom: 2rem;
      border-bottom: 2px solid #EB630D;;
      margin-bottom: 2rem;
      transition: .5s;
      @media screen {
        @media (max-width: 1330px) {
          flex-direction: column;
        }
      }
      &:last-child {
        margin-bottom: 0;
        border: none;
      }
      &:nth-child(even) {
        flex-direction: row-reverse;
        @media screen {
          @media (max-width: 1330px) {
            flex-direction: column;
          }
        }
        .main-news-list-li-description {
          margin-left: 0;
          margin-right: 32px;
          @media screen {
            @media (max-width: 1330px) {
              margin-left: 0;
              margin-right: 0;
              margin-top: 32px;
              @media (max-width: 640px) {
                margin-top: 20px;
              }
            }
          }
        }
      }
      &-description {
        margin-left: 32px;
        @media screen {
          @media (max-width: 1330px) {
            margin-left: 0;
            margin-right: 0;
            margin-top: 32px;
            @media (max-width: 640px) {
              margin-top: 20px;
            }
          }
        }
      }
      &-date {
        display: block;
        margin-top: 20px;
        @media screen {
          @media (max-width: 1330px) {
            margin-top: 18px;
            @media (max-width: 640px) {
              margin-top: 12px;
            }
          }
        }
      }
      &-img {
        width: 304px;
        min-width: 304px;
        height: 304px;
        box-shadow: 10px 10px 24px #C5C5C5;
        @media screen {
          @media (max-width: 1330px) {
            display: block;
            margin-left: auto;
            margin-right: auto;
            @media (max-width: 640px) {
              width: 280px;
              min-width: 280px;
              height: 280px;
            }
          }
        }
      }
    }
  }
}
.main-news-list-li.show {
  opacity: 1;
  transform: scale3d(1, 1,1);
}
//end main-news
//start video
.main-videos-container {
  margin-top: 1rem;
  display: flex;
  flex-wrap: wrap;
  &-item {
    width: 416px;
    margin-top: 40px;
    margin-right: 32px;
    transition: .5s;
    @media screen {
      @media (max-width: 1330px) {
        width: 300px;
        margin-right: 20px;
        &:nth-child(3n) {
          margin-right: 20px;
        }
        &:nth-child(even) {
          margin-right: 0;
        }
        @media (max-width: 640px) {
          margin-right: 0 !important;
          width: 280px;
          margin-top: 32px;
        }
      }
    }
    &:nth-child(3n) {
      margin-right: 0;
    }
    &-frame {
      width: 100%;
      height: 234px;
      border-radius: 4px;
      overflow: hidden;
