.popup {
  background: rgba(102, 102, 102, 0.9);
  position: fixed;
  top: 0;
  left: 0;
  z-index: 100;
  width: 100%;
  height: 100vh;
  justify-content: space-between;
  display: none;
}
.modal-container {
  margin-top: 58px;
  width: 100%;
  @media screen {
    @media (max-width: 640px) {
      margin-top: 37px;
    }
  }
  &-top {
    height: 48px;
    display: flex;
    justify-content: space-between;
    @media screen {
      @media (max-width: 1330px) {
        height: 39px;
      }
    }
    &-cross {
      width: 24px;
      height: 24px;
      cursor: pointer;
      align-self: center;
      @media screen {
        @media (max-width: 640px) {
          width: 20px;
          height: 20px;
          margin-left: auto;
        }
      }
    }
    &-left {
      display: flex;
      @media screen {
        @media (max-width: 640px) {
          display: none;
        }
      }
      &-tab {
        padding: 10px 35px;
        border-radius: 4px 4px 0 0;
        background: transparent;
        display: block;
        color: #F3F3F3;
        font-weight: 400;
        @media screen {
          @media (max-width: 1330px) {
            font-size: .8rem;
            line-height: .95rem;
            padding: 10px 20px;
          }
        }
        &.selected {
          color: #666666;
          background: #F3F3F3;
        }
      }
    }
  }
  &-bottom {
    background: #F3F3F3;
    padding: 20px 40px 32px 40px;
    max-height: calc(100vh - 58px - 48px);
    overflow-y: auto;
    overflow-x: hidden;
    @media screen {
      @media (max-width: 1330px) {
        padding: 20px;
        &::-webkit-scrollbar {
          width: 0px;
          background: transparent;
        }
        @media (max-width: 640px) {
          padding: 16px 10px 28px 10px;
        }
      }
    }
    &-container {
      display: none;
      @media screen {
        @media (max-width: 640px) {
          display: none !important;
        }
      }
      &.selected {
        display: block;
      }
    }
  }
  .pdf-row-main {
    color: #EB630D;
    font-weight: 400;
    @media screen {
      @media (max-width: 640px) {
        font-size: 1rem !important;
        line-height: 1.18rem !important;
      }
    }
  }
  .pdf-row-between {
    margin-top: 0;
    width: 950px;
    @media screen {
      @media (max-width: 1330px) {
        width: 100%;
      }
    }
    .pdf-row-mini-item:nth-child(n+3) {
      margin-top: 4px;
    }
    .pdf-row-item-container {
      margin-top: 4px;
      @media screen {
        @media (max-width: 1330px) {
          margin-top: 10px;
          width: 256px;
          display: flex;
          justify-content: space-between;
          .pdf-row-item-image-btn {
            margin-right: 0;
            height: 30px;
            width: 123px;
          }

        }
      }
    }
  }
  .pdf-row-item-image-btn {
    box-shadow: 5px 5px 12px #C5C5C5, -5px -5px 12px #FFFFFF;
    background: #fff;
    &:hover {
      box-shadow: 5px 5px 12px #C5C5C5, -5px -5px 12px #FFFFFF;
      background: #fff;
    }
  }
}
